@import url(https://fonts.googleapis.com/css2?family=Dosis&display=swap);
* {
  margin: 0;
  padding: 0;
}
/* font-family: 'BOG 2017 Headline'; */
/* font-family: 'BOG 2018' */

@font-face {
  font-family: "BOG 2017 Headline";
  src: url(/billing/static/media/BOG2017Headline-SemiBold.9f53f43e.eot);
  src: url(/billing/static/media/BOG2017Headline-SemiBold.9f53f43e.eot?#iefix)
      format("embedded-opentype"),
    url(/billing/static/media/BOG2017Headline-SemiBold.5cdc261c.woff2) format("woff2"),
    url(/billing/static/media/BOG2017Headline-SemiBold.fed7a1f0.woff) format("woff"),
    url(/billing/static/media/BOG2017Headline-SemiBold.a48f87da.ttf) format("truetype");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "BOG 2018";
  src: url(/billing/static/media/BOG2018-Medium.f8b4434f.eot);
  src: url(/billing/static/media/BOG2018-Medium.f8b4434f.eot?#iefix)
      format("embedded-opentype"),
    url(/billing/static/media/BOG2018-Medium.c3415d90.woff2) format("woff2"),
    url(/billing/static/media/BOG2018-Medium.aaeb5928.woff) format("woff"),
    url(/billing/static/media/BOG2018-Medium.28bac2d7.ttf) format("truetype");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
body {
  background-color: #F8F8FC;
}
li {
  list-style: none;

}

/*Checkbox*/
.ant-checkbox-wrapper {
  font-family: "BOG 2018";
  font-size:12px !important;
}

/*Form label error*/
.ant-form-item-explain-error {
  font-family: "BOG 2018";
  font-size:12px !important;
}
/*Form label*/
.ant-form-item-required {
  font-family: "BOG 2018";
  font-size:12px !important;
}
/* Drawer */
.ant-drawer-title {
  font-family: "BOG 2017 Headline";
  font-size:12px !important;
}



.input_password{
  padding:5px !important;
  border-radius: 4px;
  border:2px solid #7279ff;
  font-family: "BOG 2018";
  font-size:12px !important;
}
.input{
  padding:5px !important;
  border-radius: 4px;
  border:2px solid #7279ff;
  font-family: "BOG 2018";
  font-size:12px !important;
  padding-left: 10px !important;
}

.steps-action {
  display: flex !important;
  justify-content: space-between !important;
  margin-top:25px;
}
